import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "./AchievementCard.scss";

export default function AchievementCarousel({ achievements, isDark }) {
  const openUrlInNewTab = (url, name) => {
    if (!url) {
      console.log(`URL for ${name} not found`);
      return;
    }
    const win = window.open(url, "_blank");
    win.focus();
  };

  const [slidesToShow, setSlidesToShow] = useState(3);

  // Adjust the number of slides based on screen width
  useEffect(() => {
    const updateSlidesToShow = () => {
      const width = window.innerWidth;
      if (width < 600) {
        setSlidesToShow(1);
      } else if (width < 900) {
        setSlidesToShow(2);
      } else {
        setSlidesToShow(3);
      }
    };

    updateSlidesToShow();
    window.addEventListener("resize", updateSlidesToShow);
    return () => window.removeEventListener("resize", updateSlidesToShow);
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: slidesToShow,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    arrows: true
  };

  return (
    <div className="carousel-container">
      <Slider {...settings}>
        {achievements.map((cardInfo, index) => (
          <div
            key={index}
            className={isDark ? "dark-mode certificate-card" : "certificate-card"}
          >
            <div className="certificate-image-div">
              <img
                src={cardInfo.image}
                alt={cardInfo.imageAlt || "Card Thumbnail"}
                className="card-image"
              />
            </div>
            <div className="certificate-detail-div">
              <h5 className={isDark ? "dark-mode card-title" : "card-title"}>
                {cardInfo.title}
              </h5>
              <p className={isDark ? "dark-mode card-subtitle" : "card-subtitle"}>
                {cardInfo.subtitle}
              </p>
            </div>
            <div className="certificate-card-footer">
              {cardInfo.footerLink.map((v, i) => (
                <span
                  key={i}
                  className={isDark ? "dark-mode certificate-tag" : "certificate-tag"}
                  onClick={() => openUrlInNewTab(v.url, v.name)}
                >
                  {v.name}
                </span>
              ))}
            </div>
          </div>
        ))}
      </Slider>
    </div>
  );
}
