import React, { Suspense, useContext, useEffect, useState } from "react";
import "./twitter.scss";
import Loading from "../loading/Loading";
import { TwitterTimelineEmbed } from "react-twitter-embed";
import { twitterDetails } from "../../portfolio";
import StyleContext from "../../contexts/StyleContext";

// Fallback loader component
const renderLoader = () => <Loading />;

export default function Twitter() {
  const { isDark } = useContext(StyleContext);
  const [loadError, setLoadError] = useState(false);
  const widthScreen = window.innerWidth; // Using innerWidth for better accuracy

  // Check for loading errors
  useEffect(() => {
    const timeout = setTimeout(() => {
      if (!document.querySelector("#twitter iframe")) {
        setLoadError(true);
      }
    }, 10000); // Wait for 10 seconds to detect loading failure
    return () => clearTimeout(timeout); // Clean up timeout on component unmount
  }, []);

  // If display is disabled or username is missing, return null
  if (!twitterDetails.display || !twitterDetails.userName) {
    return null;
  }

  return (
    <Suspense fallback={renderLoader()}>
      <div className="tw-main-div" id="twitter">
        <div className="centerContent">
          {loadError ? (
            <div className="error-message">
              <h2>Can't load? Check privacy protection settings</h2>
            </div>
          ) : (
            <TwitterTimelineEmbed
              sourceType="profile"
              screenName={twitterDetails.userName}
              options={{ height: 400, width: widthScreen }}
              placeholder={renderLoader()}
              autoHeight={false}
              borderColor="#fff"
              theme={isDark ? "dark" : "light"}
              noFooter={true}
              noBorders={true}
            />
          )}
        </div>
      </div>
    </Suspense>
  );
}
